
.eventBody{
    margin-top:20px;
    margin-left:auto;
    margin-right: auto;
    padding-bottom: 25vh;
  }
  
  .eventYearWrapper{
    margin-top:20px;
    width: 50vw;
  }
  .eventYearText{
    font-family: 'Lato-Regular', sans-serif;
    font-weight: 400;
    font-size: 30px;
    line-height: 32px;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
  } 

.eventItemBody {
    background-color: #e0e0e0fa;
    margin-top:20px;
    border-style: solid;
    border-width: 2px;
    border-radius: 20px;
    min-height: 150px;
    width: 50vw;
    margin-left:auto;
    margin-right: auto;
    box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }

.eventItemImageBody{
    background-color: #e0e0e0fa;
    margin-top:20px;
    border-style: solid;
    border-width: 2px;
    border-radius: 20px;
    min-height: 250px;
    width: 50vw;
    margin-left:auto;
    margin-right: auto;
    box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }

