  .eventItemTitle{
    font-family: 'Lato-Regular', sans-serif;
    font-weight: 400;
    font-size: 20px;
    line-height: 25px;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    margin-bottom: 10px;
  }

  .eventItemDescText{
    font-family: 'Lato-Regular', sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 15px;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
  }

  .eventItemDescWrapper{
    margin: 15px;
  }

  .eventsInfoBody{
    margin-top: 50px;
  }

  .image-gallery {
  margin: 0 auto;
  width: 65%;
